import { Component, OnInit } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {DataService} from '../data.service';
import {Productsfetch} from '../product';
import {ActivatedRoute, Router} from '@angular/router';

@Component({
  selector: 'app-yourproducts',
  templateUrl: './yourproducts.component.html',
  styleUrls: ['./yourproducts.component.css']
})
export class YourproductsComponent implements OnInit {

  pro: Productsfetch[];
  constructor(private http: HttpClient, private data: DataService,
              private router: ActivatedRoute,
              private route: Router) { }
              
  ngOnInit() {
   this.load();
   }

  load() {
    this.http.post('http://localhost:2000/api/remove', {User_id: localStorage.getItem('userId')}).subscribe((data: any) => {
      console.log(data);
      this.pro = data;
    });
  }

 
  remove_book(data1) {
      this.http.post('http://localhost:2000/api/remove/remove_complete', {User_id : localStorage.getItem('userId'), Product_id: data1}).subscribe((data: any) => {
       this.load();   
    }) ;
  
  }
  

}
