import { Component, OnInit } from '@angular/core';
import {Productsfetch,filter} from '../product';
 import { DataService } from '../data.service';
 import { HttpClient, HttpHeaders } from '@angular/common/http';
 import {Router,ActivatedRoute} from '@angular/router';
 import {Pipe} from '@angular/core';
import { ValueTransformer } from '@angular/compiler/src/util';

@Component({
  selector: 'app-listings',
  templateUrl: './listings.component.html',
  styleUrls: ['./listings.component.css']
})
export class ListingsComponent implements OnInit {

 pro: Productsfetch[];
 filters : filter
 bookcondition=["New","Almost New","Slight Damage","Worn"]

constructor(private _dataService: DataService,
   private http: HttpClient,
   private router: ActivatedRoute,
    private route: Router,
  ) { }


getItems(){
  this._dataService.getProducts().subscribe(Prod =>{this.pro = Prod;
    console.log(this.pro);

   })
 }
  ngOnInit() {
    this.getItems()
    this.filters = {
      condition :""
  }
 }



 listingsMenu(id){
   console.log(id);
   this.route.navigate(['listingid',id]);
 }
 sortbyprice(price : number){
  if(price == 1){
  this._dataService.getbypricelow().subscribe(Prod =>{this.pro = Prod;
    console.log(this.pro);
  })
}
  else if (price == 2){
    this._dataService.getbypricehigh().subscribe(Prod =>{this.pro = Prod;
      console.log(this.pro);
    })
}

}

sortFilter(shape){
  this.filters = {
    condition : shape.value
  }

      console.log(this.filters)

    this.http.post('/api/products/filter',this.filters).subscribe((data:any) => {
      this.pro = data
      console.log(data);
    });

 }

 onSearchChange(book :string ){

  this.http.post('/api/products/filterbybook',{bookname : book}).subscribe((data:any) => {
    this.pro = data
    console.log(data);
  });
  }
  onSearchAuthor(author : string){
    this.http.post('/api/products/filterbyAuthor',{authorname : author}).subscribe((data:any) => {
      this.pro = data
      console.log(data);
    });
  }


}

