import { Component, OnInit } from '@angular/core';
import { messagefetch } from '../product';
import { HttpClient, HttpHeaders } from '@angular/common/http';
@Component({
  selector: 'app-message',
  templateUrl: './message.component.html',
  styleUrls: ['./message.component.css']
})
export class MessageComponent implements OnInit {
wishfetchmessage : messagefetch[]
  constructor(private http: HttpClient) { }

  ngOnInit() {
    this.http.post('/api/message/fetchmessages', {sellerid: localStorage.getItem('userId')}).subscribe((data:any) => {
      console.log(data)
   this.wishfetchmessage = data
   console.log(this.wishfetchmessage)
      if(data['message']){
        alert("You dont have ant message")
      }
    })
  }
  onSend(data, data1, data2, data3) {
    this.http.post('/api/message/reply', {id: data3, userid: localStorage.getItem('userId'), productid: data1, sellerid: data2, message: data, status: 1}).subscribe((data:any) => {
      console.log(data)
      if(data) {
        alert('message sent')
        this.ngOnInit()
      }

    })
  }

}
