import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {FormBuilder, FormGroup} from '@angular/forms';
import {HttpClient} from '@angular/common/http';

@Component({
  selector: 'app-chngpwd',
  templateUrl: './chngpwd.component.html',
  styleUrls: ['./chngpwd.component.css']
})
export class ChngpwdComponent implements OnInit {
  show: Boolean;
  myform1: FormGroup;
  constructor(
    private fb: FormBuilder,
    private http: HttpClient,
    private router: ActivatedRoute,
    private route: Router
  ) { }

  ngOnInit() {
    this.myform1 = this.fb.group({
      email: localStorage.getItem('email'),
      password: '',
      repassword: ''
    });
  }

  onSubmit() {
    const values = this.myform1.value;

    console.log(values);
    this.postdata(values);
  }

  postdata(data1) {
    console.log(data1.password);
    if (data1.password === data1.repassword) {
      this.http.post('http://localhost:2000/api/chngpwd', data1).subscribe((data: any) => {
        if (data['message']) {
          alert('Password Successfully Changed');
          localStorage.removeItem('email')
          this.route.navigate(['login']);
        } else {
          alert('User not found, Please Enter Correct Details.');
        }
      });
    } else {
      alert('Password does not match');
    }
  }


}
