import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import {HttpClient, HttpParams, HttpHeaders} from "@angular/common/http";
import {DataService} from "../data.service";
@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.css']
})
export class AddComponent implements OnInit {
  myform : FormGroup
  constructor( private fb: FormBuilder,
    private http: HttpClient,
    private data: DataService
    ) { }
    fileToUpload: File = null
    emailbind = localStorage.getItem('email')
  ngOnInit() {
    this.myform = this.fb.group({
      bname:"",
      aname:"",
      email:"",
      description:"",
      price:"",
      condition:"",
      sellerId: localStorage.getItem('userId'),
  });

  }

  handleFileInput(files: FileList) {
    this.fileToUpload = files.item(0);
    //this.uploadFileToActivity();
  }
  onSubmit(){
    const values = this.myform.value;

    console.log(values);
    this.postdata(values,this.fileToUpload)

  }
   postFile(name) {
     const formData: FormData = new FormData();
     formData.append('fileName', this.fileToUpload, name);
     this.http
       .post('/image', formData)
       .subscribe((data:any) => {
         console.log(data);
       });
 }
  postdata(data,fileToUpload: File){
    this.http.post('/api/products',data).subscribe((data:any) => {
      this.postFile(data.id);
      console.log(data);
      if(data){
        alert('successfully added')
      }
    });

}

}
