import { Component, OnInit,Pipe, PipeTransform,Output,EventEmitter } from '@angular/core';
import {ParamMap} from "@angular/router";
import {Router,ActivatedRoute} from '@angular/router';
import {Productsfetch} from '../product';
 import { DataService } from '../data.service';
 import { HttpClient,HttpHeaders } from '@angular/common/http';
 import { wishlist} from '../product';
@Component({
  selector: 'app-listingid',
  templateUrl: './listingid.component.html',
  styleUrls: ['./listingid.component.css']
})
export class ListingidComponent implements OnInit {
  selected: number;
  list_id_pro: Productsfetch[];
  seller: string;

   counter: number = 1;

@Output () public MessageEvent = new EventEmitter();

  constructor(
    private _dataService: DataService,
    private router: ActivatedRoute,
    private http: HttpClient,
    private route: Router
  ) { }


  getItems(data1) {
    this.http.post('/api/products/getbyid', {Product_id: data1}).subscribe((data: any) => {
      this.list_id_pro = data;
      console.log(this.list_id_pro);
    });
   }

  ngOnInit() {
    {
      this.router.paramMap.subscribe( (params: ParamMap) =>{
        this.selected = +(params.get('id'));
        this.getItems(this.selected);
      });
    }
  }



  sendMessage(){
    console.log(this.list_id_pro[0]['sellerId'].toString());

    if(localStorage.getItem('userId') !== this.list_id_pro[0]['sellerId'].toString())
    {
      this.http.post('/api/wish', {User_id: localStorage.getItem('userId'), Product_id: this.selected}).subscribe((data:any) => {
     // console.log(data)

     if(data['message']){
       alert("you have already added product to wishlist");

     }
     else{
      alert("add to wishlist successfully!");
     }


    })

  }
  else if(localStorage.getItem('userId') === this.list_id_pro[0]['sellerId'].toString()) {
      alert("You can not add your product")
    }
    else
    {
      alert("please log in")
    }

 // console.log(this.counter);
    //this.MessageEvent.emit('hello');

  }


  sendMessageCart(){
    //console.log(this.selected);

    if(localStorage.getItem('userId') !== this.list_id_pro[0]['sellerId'].toString())
    {
      this.http.post('/api/cart',{User_id:localStorage.getItem('userId'),Product_id:this.selected}).subscribe((data:any) => {
        // console.log(data)

        if(data['message']){
          alert("you have already added product to cart");

        }
        else{
          alert("add to cart successfully!");
        }


      })

    }
    else if(localStorage.getItem('userId') === this.list_id_pro[0]['sellerId'].toString()) {
      alert("You can not add your product")
    }
    else
    {
      alert("please log in")
    }

    // console.log(this.counter);
    //this.MessageEvent.emit('hello');

  }


  onSend(data){

    console.log(data)
    if(localStorage.getItem('userId') !== this.list_id_pro[0]['sellerId'].toString()) {

      this.http.post('/api/message', {
        userid: localStorage.getItem('userId'),
        productid: this.selected,
        sellerid: this.list_id_pro[0]['sellerId'],
        message: data,
        status : 0,
 }).subscribe((data: any) => {
        console.log(data)
        if (data) {
          alert('message sent')
        }

      })
    }
    else if(localStorage.getItem('userId') === this.list_id_pro[0]['sellerId'].toString()) {
      alert("You can not send message to your self")
    }
    else
    {
      alert("please log in")
    }
}


}
